import React, {useState,useEffect} from 'react'
import { NavLink } from 'react-router-dom';
import Header from './Header'; 
import Axios from "axios";
import "./home.css";
import ReactStars from "react-rating-stars-component";
export const Home = () => {
    const [uname, setuname] = useState("");
    const [feedback, setfeedback] = useState("");
    const [rating, setrating] = useState(1);
    const [dp,setdp]=useState(null);
    const [reviews,setreviews]=useState([]);
      useEffect(()=>{
      Axios.get("http://portal-fiduciary.herokuapp.com/read").then((response)=>{
          console.log(response);
        setreviews(response.data);
      });
     },[]);
    const openForm=()=>{
          document.getElementsByClassName("correct")[0].style.display="flex"
    }
   const closeForm=()=>{
          document.getElementsByClassName("correct")[0].style.display="none"
    }
    const ratingChanged = (newRating) => {
     console.log(newRating);
     setrating(newRating);
    };
    const submit=(e)=>{
        e.preventDefault();
         var fd=new FormData();
       fd.append("dp",dp);
       fd.append("uname",uname);
       fd.append("feedback",feedback);
       fd.append("rating",rating);
           Axios.post("http://portal-fiduciary.herokuapp.com//create",fd); 

    }
    return (
        <>
        <Header />
        <div className="row m-4  ">
               {reviews.map((val,key)=>{
              return(
              
              <div className="reviewbox col-lg-4 col-sm-12">
                 <div className="fl">  
                 <div> 
                 
                 <img src={`http://portal-fiduciary.herokuapp.com//img/${val.dp}`} id="img"></img> 
                 </div>
                <div className="container"> 
                <h1>{val.username}</h1> 
                 <ReactStars
               count={val.rating}
              
               size={25}
               color="#ffd700"
               />
                <p>{val.feedback}</p> 
              
                

                <button /* onClick={()=>{Delete(val._id) }}*/ className="btn-danger">Delete</button> 
              </div>
             </div>
             </div>
              );
            })}

        </div>
        <button onClick={openForm} className="btn btn-primary button">Add data </button>
 
 
 
 
                       {/* ********** Modal Box */}
 <div className=" container correct  "  >
  <div class="modal-dialog wr " role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title text-warning">Review</h5>
         <ReactStars
               count={5}
               onChange={ratingChanged}
               size={35}
               activeColor="#ffd700"
               />
        </div>
      <div class="modal-body">
         <form   >
                 <div className="form-group">
                    <label className="mb-1">Username</label>
                    <input value={uname} onChange={(e)=>setuname(e.target.value)}  type="text" className="form-control" placeholder="Enter username" />
                </div>

                <div className="form-group">
                    <label className="mb-1">Your feedback</label>
                    <textarea value={feedback} onChange={(e)=>setfeedback(e.target.value)}  placeholder="Enter feedback!!" class="form-control" id="exampleFormControlTextarea1" ></textarea>

                </div>
              <div className="form-group">
               <label className="mb-1" >  UserProfile
               </label>
               <input
               name="dp"
               type="file"
              
              placeholder="Enter the title of your Note"
              className="w-75 form-control"
              id="dp"
             onChange={(e)=>setdp(e.target.files[0])}
             />
            </div>
     </form>
      </div>
      <span class="modal-footer">
         <button onClick={closeForm} className="btn text-white btn-get-started btn-danger" >Close</button>

         <button onClick={submit} className="btn text-white btn-get-started btn-warning" >Submit</button>
      </span>
    </div>
  </div>
</div>
        </>
    )
}
export default Home;