import React,{useState,useContext} from 'react'
import Modal from 'react-modal'
import { NavLink } from 'react-router-dom';
import "./login.css"
import Header from './Header'; 
 import {checkapi} from '../App'
const Login = () => {
     const [uid,setuid]=useState("");
     const [pwd,setpwd]=useState("");
    const check = useContext(checkapi);
     const submit=(e)=>{
         e.preventDefault();
         // I will keep user id as "adarsh" and pwd "123456"
         console.log(uid==="adarsh@12");
         if(uid==="adarsh@12" && pwd==="123456"){
            check();
          document.getElementsByClassName("correct")[0].style.display="flex"
            setpwd("");
            setuid("");
           
         }
         else{
            check();
            document.getElementsByClassName("wrong")[0].style.display="flex"
            setpwd("");
            setuid("")
         }
     }
       const close=()=>{
        document.getElementsByClassName("wrong")[0].style.display="none"
    }
    return (
        <React.Fragment>
           <div className="bg">
             <Header />
            <div className=" cont mx-auto mt-5 p-4">
            <form className="text-white" onSubmit={submit}>
                <h3 className="text-center">Sign In</h3>

                <div className="form-group">
                    <label>User Id</label>
                    <input onChange={(e)=>setuid(e.target.value)} value={uid} type="email" className="form-control" placeholder="Enter userId" />
                </div>

                <div className="form-group">
                    <label>Password</label>
                    <input onChange={(e)=>setpwd(e.target.value)} value={pwd} type="password" className="form-control" placeholder="Enter password" />
                </div>
                 <button type="submit" className="btn btn-primary  mt-2">Sign In</button> 
               
                
            </form>
        </div>
               <div className=" correct container"  >
  <div class="modal-dialog wr w-25" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">Message</h5>
        </div>
      <div class="modal-body">
        Login Successfully!!!
      </div>
      <div class="modal-footer">
      <NavLink to="/main" className="btn text-white btn-get-started w-25 btn-warning">OK</NavLink>
      </div>
    </div>
  </div>
</div>
<div className=" wrong container"  >
  <div class="modal-dialog wr" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">Message</h5>
        </div>
      <div class="modal-body">
        Incorrect UserId or Password!!
      </div>
      <div class="modal-footer">
      <button type="button" style={{height:"5vh"}} className="text-white  btn-warning" onClick={close}>Close</button>
      </div>
    </div>
  </div>
</div>
   </div>        
        </React.Fragment>
    );
}

export default Login;